<template>
  <div id="school__detail">
    <common-header title="申请详情"></common-header>
    <template v-if="!hasInfo">
    	<div>
    		暂无详情
    	</div>
    </template>
    
    <template v-if="hasInfo">
	    <div class="detail__user border">
	      <div class="user__left">
	        <div class="user__left__img">
	          <img :src="applyInfo.headImagePath" alt="">
	        </div>
	        <div class="user__left__info">
	          <span class="info__name">{{applyInfo.submiterName}}</span>
	          <span class="info__no">
	            学校排名：{{applyInfo.schollRanking}}
	          </span>
	        </div>
	      </div>
	      <div class="user__right">
	        <div class="user__right__count">
	          <i class="count active"></i>
	          <span>{{applyInfo.praiseStat}}</span>
	        </div>
	        <div class="user__right__rank">校内排名：{{applyInfo.myRanking}}</div>
	      </div>
	    </div>
	
	    <div class="link_cell border">
	      <span class="title">学校名称</span>
	      <span class="value">{{applyInfo.activityOrgName}}</span>
	    </div>
	
	    <div class="link_cell border">
	      <span class="title">申请人姓名</span>
	      <span class="value">{{applyInfo.submiterName}}</span>
	    </div>
	
	    <div class="link-cell-auto border">
	      <span class="title">队员姓名</span>
	      <span class="value">{{applyInfo.memberNames}}</span>
	    </div>
	
	    <div class="detail__content border">
	      <div class="detail__content__title">创业说明</div>
	      <div class="detail__content__text">{{applyInfo.applyDesc}}</div>
	    </div>
	
	    <div class="detail__img">
	      <div class="detail__img__title">图片</div>
	      <div class="detail__img__list">
	        <div class="image" v-for="(item,index) in applyInfo.images">
	          <img :src="item.imagePath" alt="" @click="preview(applyInfo.images,index)">
	        </div>
	      </div>
	    </div>
    </template>
    
    <div class="detail__submit" @click="showPopup" v-if="action==2">邀请好友支持</div>
    <div class="detail__submit" v-if="action==1 && !praised" @click="praiseSwitch($route.query.id)">支持Ta</div>
    <div class="detail__submit" v-if="action==1 && praised">已支持</div>
  </div>
</template>

<script>
	
  import Cell from '@/components/Cell/Cell';

  export default {
  	data(){
  		return{
  			applyInfo:{},
  			sharing:{},
  			action:this.$route.query.action,
  			praised:'',
  			hasInfo:false
  		}
  	},
  	created(){
  		this.getApplyInfo()
  	},
  	methods:{
  		getApplyInfo(){
  			let action = this.$route.query.action
  			let id = this.$route.query.id
  			this.$Api.getApplyInfo(action,id).then((res)=>{
  				console.log(res)
  				if(res.q.s == 0){
  					this.applyInfo = res.q.applyInfo
  					this.sharing = res.q.applyInfo.sharing
  					this.invite()
  					this.hasInfo = true
  				}else{
  					this.$toast(res.q.d)
  					this.hasInfo = false
  				}
  			})
  		},
  		praiseSwitch(id){
  			let action = 12
  			let open = 1
  			this.$Api.PraiseSwitch(action,open,id).then((res)=>{
  				console.log(res)
  				if(res.q.s == 0){
  					this.$toast('点赞成功')
  					this.praised = true
  				}
  			})
  		},
  		showPopup(){
  			this.$popup({
  				title:'邀请好友支持',
  				content:'请点击右上角微信分享给好友',
  				showCancel:false,
  			}).then((res)=>{
  				if(res == 'sure'){
  					
  				}
  			})
  		},
  		//邀请好友
  		invite(){
				let params = {
					title :this.sharing.title,
					desc  :this.sharing.shareText,
					link  :"https://"+location.hostname+this.sharing.url+"&fromShare=1",
					imgurl:this.sharing.iconPath
				}
	//				console.log(this.$wechat.iniShare)
				this.$wechat.iniShare(params)
  		}
  	},
    components: {
      Cell
    }
  }
</script>


<style lang="scss">
  #school__detail {
    min-height: 100%;
    padding-bottom: 2rem;
    background: #f0f0f0;

    .detail__user {
      display: flex;
      justify-content: space-between;
      padding: .27rem .4rem;
      height: 1.6rem;
      background: #fff;
      box-sizing: content-box;

      .user__left {
        display: flex;
        height: 1.6rem;

        .user__left__img {
          width: 1.6rem;
          height: 1.6rem;
          margin-right: .4rem;
          
          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }

        .user__left__info {
          display: flex;
          padding: .2rem 0;
          flex-direction: column;
          justify-content: space-around;
          height: 1.6rem;
          font-size: 0.37rem;
          color: #666;
        }
      }

      .user__right {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 1.6rem;
        padding: .2rem 0;
        font-size: 0.37rem;
        color: #666;

        .count {
          display: inline-block;
          margin-right: .2rem;
          width: 0.37rem;
          height: 0.37rem;
          background: url('../../../assets/school/like_un.png') no-repeat center/100%;

          &.active {
            background: url('../../../assets/school/like.png') no-repeat center/100%;
          }
        }
      }
    }

    .link_cell {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0 0.4rem;
      height: 1.2rem;
      line-height: 1.2rem;
      background: #fff;
      font-size: 0.427rem;
      color: #333;

      .value {
        color: #666;
        font-size: 0.37rem;
      }
    }

    .link-cell-auto {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: .4rem;
      min-height: 1.2rem;
      background: #fff;
      font-size: 0.427rem;
      color: #333;

      .value {
        display: inline-block;
        width: 6.65rem;
        color: #666;
        font-size: 0.37rem;
        text-align: right;
      }
    }

    .detail__content {
      padding: .4rem;
      background: #fff;

      .detail__content__title {
        font-size: 0.4rem;
        padding-bottom: .27rem;
        color: #333;
      }

      .detail__content__text {
        font-size: .37rem;
        color: #666;
      }
    }

    .detail__img {
      padding: .4rem;
      background: #fff;
      
      .detail__img__title {
        font-size: 0.4rem;
        padding-bottom: .27rem;
        color: #333;
      }

      .detail__img__list {
        display: flex;
        flex-wrap: wrap;

        .image {
          display: inline-block;
          padding-bottom: .4rem;
          width: 25%;
          text-align: center;

          img {
            width: 2rem;
            height: 2rem;
          }
        }
      }
    }

    .border {
      border-bottom: 1px solid #eee;
    }

    .detail__submit {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 10rem;
      height: 1.31rem;
      font-size: 0.45rem;
      line-height: 1.31rem;
      color: #fff;
      text-align: center;
      background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
    }
  }
</style>




